body {
  margin: 0;
  padding: 0;
  text-align: left;
  font-family: sans-serif;
}

h1, h2, h3, h4, h5, h6, a{
  color: red;
}

h4, p, .form-group{
  padding-top: 1rem;
}

label {
  text-align: left;
  width: 100%;
  max-width: 500px;
  color: #847878;
  font-weight: bold;
}

@-webkit-keyframes entrance {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes entrance {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

@-webkit-keyframes logo {
  0% { opacity: 0; max-height: 100vh; }
  50% { opacity: 1; max-height: 100vh; }
  100% { opacity: 1; max-height: 250px; }
}

@keyframes logo {
  0% { opacity: 0; max-height: 100vh; }
  50% { opacity: 1; max-height: 100vh; }
  100% { opacity: 1; max-height: 250px; }
}

@-webkit-keyframes fade {
  0% {opacity: 1; height: 100vh; width: 100vw;}
  50% {opacity: 1; height: 100vh; width: 100vw;}
  99% {height: 100vh; width: 100vw;}
  100% {opacity: 0; height: 0px; width: 0px; }
}

@keyframes fade {
  0% {opacity: 1; height: 100vh; width: 100vw;}
  50% {opacity: 1; height: 100vh; width: 100vw;}
  99% {height: 100vh; width: 100vw;}
  100% {opacity: 0; height: 0px; width: 0px; }
}

.component{
  -webkit-animation-name: entrance;
          animation-name: entrance;
  -webkit-animation-duration: .1s;
          animation-duration: .1s;
}

.fade{
  top: 0px;
  left: 0px;
  z-index: 2;
  position: fixed;
  opacity: 0;
  -webkit-animation-name: fade !important;
          animation-name: fade !important;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  background-color: white;
}

.iwwlogo{
  position: relative;
  z-index: 3;
  opacity: 1;
  -webkit-animation-name: logo !important;
          animation-name: logo !important;
  -webkit-animation-duration: .75s;
          animation-duration: .75s;
  max-height: 250px;
  margin-bottom: 20px;
}

.navlinks a{
  padding: .375rem .75rem;
  display: inline-block;
}

.navlinks a:hover{
  text-decoration: none;
  color: red;
}

.poster_holder a{
  text-decoration: none;
}

.poster_holder h5{
  color: #847878;
}

.poster_holder h5:hover{
  color: red;
}

.body{
  margin: 10px 5%;
}

.body > div {
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
  padding: 0px 10px;
}

iframe{
  margin: 0px -10px;
  padding-top: 1rem;
}

.Header{
  border-bottom: 3px solid #847878;
  text-align: center;
}

.Footer{
  padding: 20px 0;
  color: #847878;
  font-size: 0.8rem;
  text-align: center;
}

.header_link{
  color: #847878;
}

.active_header_link{
  color: red !important;
  font-weight: bold;
}

.leftaligned{
  text-align: left;
}

.gdcfirstpara{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.modalbutton{
  display: none;
}

.header_logo_title{
  border-radius: 0px 0px 100vh 175px;
  /*-moz-border-radius: 0px 0px 174px 175px;
  -webkit-border-radius: 0px 0px 174px 175px;
  border: 1px solid #000000;*/
}
.title_of_page{
  position: relative;
  z-index: 3;
}

/*BELOW IS CURVE*/
.curveholder{
  height: 1px;
}
.curveholder svg{
  width: 100%;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}
/*BELOW IS CURVE*/
