/*END CURVE*/

@media (max-width: 640px) {
  .header_logo_title{
    margin-bottom: 15px;
  }

  .hiddenmodal{
    margin-left: -100px;
  }

  .shownmodal{
    z-index: 5;
  }

  .imgtest{
    margin: 0 auto;
  }

  .gdcfirstpara{
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .body{
    margin: 10px 0%;
  }

  .navlinks {
    -webkit-box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.21);
    box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.21);
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    position: fixed;
    left: 0;
    z-index: 999;
    top: 0;
    /*background-color: white;*/
    width: 100px;
    border-radius: 0 0 10px 0;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
  }

  .navlinks a{
    width: 100%;
    color: black;
    border-bottom: 1px solid #847878;
  }

  .modalbutton{
    -webkit-box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.21);
    box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.21);
    border-radius: 0 10px 10px 0;
    padding: 30px 10px;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    display: block;
    position: fixed;
    top: 0;
    left: 100px;
    z-index: 99;
    font-size: 30px;
    color: white;
    background-color: red;
    width: 30px;
  }

  .smallarrowleft {
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    z-index: 9999;
    position: fixed;
    top: 10px;
    left: 110px;
  	width: 0;
  	height: 0;
  	border-top: 20px solid transparent;
  	border-right: 10px solid white;
  	border-bottom: 20px solid transparent;
  }
  .smallarrowright {
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    z-index: 9999;
    position: fixed;
    top: 10px;
    left: 10px;
  	width: 0;
  	height: 0;
  	border-bottom: 20px solid transparent;
  	border-left: 10px solid white;
  	border-top: 20px solid transparent;
  }
}



